      },
      info:{
        enabled: false,
        nodeContentsFunction: function(itemData, item) {
          return `<div style='margin:auto; width:200px; height:100%; padding': 5px;>
                    <h3 style='font-weight: 300; font-size: 15px; color: #2f256e; padding-bottom: 3px; margin:0px'>12312312312312</h3>
                    <p style='font-size: 13px;font-family: Arial, Helvetica, sans-serif;font-weight: 300;padding:5px'>1231231312312</p>
                  </div>`
        }
      },
      style: {
        nodeStyleFunction: function(node) {
          console.log(node)
          node.aura = node.data.auras
          node.fillColor = nodeClasses[0].style.fillColor
          node.image = require(`./../../assets/images/zoomchart/${nodeClasses[0].style.image}`)
          if (node.data.main) {
            node.labelStyle.backgroundStyle.fillColor = "red"
          }

          // add plus marks on nodes that have hidden links
          if (node.dataLinks.length > node.links.length) {
            node.items = [{ px: 0, py:-1, text: '+', backgroundStyle:{ fillColor: 'orange' } }]
          } else {
            node.items = []
          }
          // if (node.hovered) node.radius = 50;
        },
        node: {
          radius: 30,
          imageCropping: true,
          // shadowBlur: 15,
          // shadowColor: "#262626",
          fillColor: 'rgba(44,233,233,0.8)'
        },
        nodeHovered: {
          shadowColor: 'white',
          shadowBlur: 15
        },
        nodeSelected: {
          lineColor: null
        },
        selection: {
          fillColor: null,
          lineColor: null
        },
        nodeFocused: {
          fillColor: 'white',
          lineColor: null,
          shadowColor: '#333',
          shadowBlur: 10
        },
        nodeLabel: {
          backgroundStyle: { fillColor: 'black' },
          textStyle: { fillColor: 'white' },
          borderRadius: 3,
          padding: 3
        },
        linkStyleFunction: function(link) {
          link.label = link.data.type
          link.labelStyle.backgroundStyle = { fillColor: link.data.fillColor, lineColor: "gray" },
          link.labelStyle.textStyle = { fillColor: link.data.textColor, lineColor: "gray" }
          link.length = 2
        },
        linkLabel: {
          borderRadius: 3,
          padding: 2
        }
      },
      legend: {
        enabled: true,
        padding: 6,
        marker: { size: 22 },
        maxLineSymbols: 12
      },
      navigation:{initialNodes:['CÔNG TY CP THÉP HÒA PHÁT HẢI DƯƠNG'], mode:'focusnodes', focusNodeExpansionRadius: 1}
      // theme: NetChart.themes.dark
    })
